* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
#loading {
  position: fixed;
  top: 0;
  width: 100%;
  height: 100vh;
  background: #fff url("Skateboarding.gif") no-repeat center center;
  z-index: 99999;
}
body {
  overflow-x: hidden;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  width: 100%;
  z-index: 10000;
  height: 10vh;
  top: 0;
  background-color: black;
}
.title {
  font-size: 25px;
  width: 50%;
  font-weight: 700;
  text-decoration: none;
  color: white;
  justify-content: flex-start;
  margin-left: 10px;
}
.nav-items {
  display: flex;
  text-transform: capitalize;
  font-size: larger;
  list-style: none;
  width: 40%;
  justify-content: space-around;
}
.nav-items li a {
  text-decoration: none;
  color: #fff;
}
#burger {
  display: none;
  cursor: pointer;
}
#burger div {
  background-color: white;
  width: 25px;
  height: 4px;
  margin-bottom: 2px;
}
.nav-items li {
  position: relative;
}
.nav-items li:before {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #fff;
  visibility: hidden;
  transform: scaleX(0);
  transition: all 0.3s ease-in-out;
}
.nav-items li:hover:before {
  visibility: visible;
  transform: scaleX(1);
}
@media screen and (max-width: 884px) {
  #burger {
    position: absolute;
    right: 15px;
    display: inline;
  }
  .nav-items {
    flex-direction: column;
    background-color: black;
    position: absolute;
    height: 90vh;
    top: 10vh;
    text-align: center;
    letter-spacing: 2px;
    right: 0;
    transition: transform 1s ease;
    transform: translate(100vw);
  }
  .nav-toggle li {
    transition: all 1s ease;
  }
  .nav-toggle {
    transform: translate(0);
  }
  .quit .line1 {
    transform: rotate(-45deg) translate(-3px, 6px);
  }
  .quit .line2 {
    opacity: 0;
  }
  .quit .line3 {
    transform: rotate(45deg) translate(-3px, -6px);
  }
}

.home {
  color: white;
  padding-top: 40vh;
  height: 200vh;
  font-size: larger;
  background-image: linear-gradient(#6a82fb, #fc5c7d);
  background-size: 100vw 100vh;
  background-repeat: no-repeat;
}
.main-title {
  font-size: 120px;
  width: 100%;
  text-align: center;
  font-family: "Caveat", cursive;
  text-shadow: 6px 2px 4px blue;
}
#home2 {
  position: absolute;
  top: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 50%);
  column-gap: 10px;
  grid-template-rows: repeat(3, 33.3vh);
  background-image: linear-gradient(#ff5f6d, #ffc371);
  height: 130vh;
  width: 100%;
  padding: 20px 10px;
}
.card {
  color: black;
  width: 80%;
}
#card1 {
  width: 50%;
  grid-column: 1/3;
}
#card2 {
  grid-column: 2/3;
}
#card3 {
  grid-column: 1/2;
  grid-row: 3/4;
}
#footer {
  position: absolute;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(#000428, #004e92);
  height: 10vh;
  width: 100%;
}
